/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 */

 .pr-carte-container {
    perspective: 1000px; // Ajout pour l'effet 3D

    .wp-block-create-block-pr-carte {
        display: flex;
        flex-wrap: wrap;
        gap: 24px;
		justify-content: flex-start;
    }
}

.pr-carte-container,
.wp-block-create-block-pr-carte {
	@media (min-width: 900px) {
		display: flex;
		flex-wrap: wrap;
		gap: 24px;
	}
}

.pr-carte {
    border-radius: 10px;
    border: 3px solid #92d16e;
    overflow: hidden;
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5);
    text-decoration: none;
    color: #232222;
    text-align: center;
    padding: 25px;
    width: 270px;
    height: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    background-color: #ffffff;
    transition: transform 1s, background-color 0.75s, border-color 0.75s;

    &-contenu {
        position: relative;
        width: 100%;
        height: 100%;
        transition: transform 1s;
        transform-style: preserve-3d;
    }

    &-front,
    &-back {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
		transition: opacity 0.3s ease, visibility 0.3s ease;
        opacity: 1;
        visibility: visible;

    }

    &-front {
        .pr-carte-icone {
			display: flex;
            justify-content: flex-end;
            fill: #478245;
        }
    }

    &-back {
		color: white;
        text-align: left;

        .pr-carte-icone {
            display: flex;
            justify-content: flex-end;
            fill: #ffffff;
        }
    }

    &-titre {
        font-size: 28px;
        margin-bottom: 8px;
    }

    &-soustitre {
        font-size: 18px;
        font-family: 'Raleway';
        margin-top: 0;
    }

    &-image {
        width: 150px;
        height: 150px;
        border-radius: 100%;
        border: 3px solid #3A5A40;
        object-fit: cover;
        object-position: center center;
    }
}
